
@media only screen and (max-width: 1200px) {
    .user-access .login-logout {
        font-size: 14px;
    }
    .colored-header-box {
        font-size: 23px;
    }
    .form-signin-or-up-container .submit-btn {
        font-size: 15px;
    }
    .colored-header-box {
        font-size: 21px;
    }
    .sign-up-form {
        margin-bottom: 10px;
        padding: 8px 13px;
    }
    .ReactPasswordStrength-input {
        font-size: 16px!important;
        padding: 9px 0px 9px 13px!important;
    }

    .ReactPasswordStrength-strength-desc {

        padding: 12px 0px!important;

        right: 4px!important;
    }
    .intl-tel-input input[type=tel] {
        padding: 8px 13px !important;
    }
    .intl-tel-input.allow-dropdown .flag-container {
        height: 41px;
    }
}
@media only screen and (max-width: 767px) {
    .form-signin-or-up-container{
        width: 290px;
    }
    .colored-header-box {

        font-size: 19px;
    }
    .form-signin-or-up-container .submit-btn {
        font-size: 14px;
        margin-top: 13px;
        margin-bottom: 17px;
    }
    .sign-up-form {
        margin-bottom: 9px;
        padding: 8px 12px;
    }
    .main-div{
        min-height: 500px;
    }
    .ReactPasswordStrength-input {
        box-sizing: border-box;
        font-size: 16px;
        padding: 10px 0 8px 12px!important;
    }
    .ReactPasswordStrength-strength-desc {
        padding: 12px 0px!important;
        right: 7px!important;
    }
    .intl-tel-input input#phonenumber{
        padding: 10px 0 8px 12px!important;
    }

    .intl-tel-input.allow-dropdown .flag-container {

        height: 43px;
    }

    .input-errors {
        margin-bottom: 5px;
        margin-top: -4px;
        display: block;
        font-size: 12px;
    }

    .iti-mobile .intl-tel-input .country-list .country {

        font-size: 11px;
    }
    .iti-mobile .intl-tel-input.iti-container {
        width: 275px;
    }

    .intl-tel-input.allow-dropdown.separate-dial-code .selected-dial-code {

        padding-left: 24px!important;
        font-size: 12px;
    }

    .intl-tel-input.allow-dropdown.separate-dial-code .selected-flag {
        width: 80px!important;
    }

}
